body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.main {
  display: flex;
  flex-flow: column wrap;
  margin: 3% 25%;
  align-items: center;
  width: 50%;
  box-shadow: 0px 0px 5px 0px;
}

.answerNav {
  display: flex;
  flex-flow: row;
  justify-content: center;
  box-shadow: 0px 1px 2px 0px;
}

.answersTab {
  padding: 15px;
  color: white;
  text-align: center;
  width: 50%;
}
.topNav {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-around;
  max-height: 100px;
  background-color: rgb(228, 228, 228);
}

.topNav a {
  color: black;
  padding: 5%;
  font-size: 1.3em;
  width: 25%;
  text-align: center;
  align-self: center;
  justify-content: center;
}



.avatar {
  max-width: 75px;
  border-radius: 50%;
}

.left {
  display: flex;
  flex-flow: row wrap;
  width: 50%;
  justify-content: flex-end;
  align-content: center;
}

.right {
  align-self: flex-end;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
}
.form {
  font: 95% Arial, Helvetica, sans-serif;
  max-width: 50%;
  margin: 4% auto;
  padding: 16px;
  background: #F7F7F7;
  box-shadow: 0px 1px 5px 0px;
}

.form h1 {
  background: #4CA0AF;
  padding: 20px 0;
  font-size: 140%;
  font-weight: 300;
  text-align: center;
  color: #fff;
  margin-top: 0;
}

.form input[type="text"] {
   outline: none;
   box-sizing: border-box;
   -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
   width: 100%;
   background: #fff;
   margin: 3% 0;
   border: 1px solid #ccc;
   padding: 2%;
   color: #555;
   font: 95% Arial, Helvetica, sans-serif;
}

.form input[type="submit"] {
   box-sizing: border-box;
   -webkit-box-sizing: border-box;
   -moz-box-sizing: border-box;
   width: 100%;
   padding: 3%;
   background: #4CA0AF;
   border-bottom: 2px solid #30C29E;
   border-top-style: none;
   border-right-style: none;
   border-left-style: none;	
   color: #fff;
}

.form input[type="submit"]:hover {
   background: #2EBC99;
}

.form h2 {
  text-align: center;
  margin: 0;
  font-family: Arial;
}
.unAnsweredQ {
  max-width: 50%;
  width: 85%;
  margin: 3% 25%;
}

.inputs {
  width: 10%;
  margin: 10px;
}

.option {
  width: 80%;
  margin: 10px;
  max-width: 85%;
}

.divider {
  width: 100%;
}

